<template>
    <section class="modal">
        <img src="@/assets/images/close.png" class="close">
        <div class="text_wrapper">
            <h1>ШАГ 1 - РЕГИСТРАЦИЯ</h1>
            <span>Добро пожаловать в сервис Intelektaz! Всего три простых шага отделяют Вас от мощного прорыва в Вашем бизнесе с нашим ИИ. Мы проведем Вас за руку к успеху. Сделайте первый шаг – заполните анкету ниже и перейдите на второй шаг нажав кнопку «Шаг 2»: </span>
        </div>
        <hr>
        <div class="modal_cotainer">
            <div class="user_info">
                <img v-if="userData" :src="require(`@/assets/images/${userData.img}`)" class="avatar">
                <div class="user_info_text">
                    <h2 v-if="userData">{{ userData.name }}</h2>
                    <span v-if="userData">{{ userData.id }}</span>
                </div>
            </div>
            <div class="inputs">
                <h2>Региональные данные</h2>
                <div class="dropdown">
                    <input
                        type="text"
                        v-model="searchQuery"
                        placeholder="Введите страну"
                        @focus="isDropdownVisible = true"
                        @blur="hideDropdown"
                    />
                    <img :class="{'rotated': isDropdownVisible}" src="@/assets/images/arrow_down.png" class="arrow_down">
                    <ul v-if="isDropdownVisible" class="dropdown-menu">
                    <li
                        v-for="country in filteredCountries"
                        :key="country.name"
                        @mousedown.prevent="selectCountry(country.name)"
                    >
                        {{ country.name }}
                    </li>
                    </ul>
                </div>
            </div>
        </div>

    </section>
</template>

<script>

    // import AppBadButton from '@/components/AppBadButton.vue';
    // import AppGoodButton from '@/components/AppGoodButton.vue';
    import { getUserInfo } from '@/services/user';

    export default {
        // components: { AppBadButton, AppGoodButton },
        data() {
            return {
                text: "ВОЙТИ",
                text2: "ОТМЕНИТЬ",
                userData: null,
                countries: [],
                searchQuery: "",
                selectedCountry: "",
                isDropdownVisible: false,
            }
        },
        computed: {
            filteredCountries() {
                return this.countries.filter((country) =>
                    country.name.toLowerCase().includes(this.searchQuery.toLowerCase())
                );
            },
        },
        methods: {
            selectCountry(country) {
                this.selectedCountry = country;
                this.searchQuery = country;
                this.isDropdownVisible = false;
            },

            hideDropdown() {
            setTimeout(() => {
                this.isDropdownVisible = false;
            }, 200);
            },
        },
        async created() {
            const response = await getUserInfo();
            this.userData = response;
            console.log(this.userData.img);

            try {
                const response = await fetch('https://restcountries.com/v3.1/all');
                const data = await response.json();
                this.countries = data.map(country => ({
                    name: country.name.common
                }));
            } catch (error) {
                console.error('Ошибка при загрузке данных о странах:', error);
            }
        },
        
    };
</script>

<style scoped>
    @font-face {
        font-family: 'Tektur';
        src: url('@/assets/fonts/Tektur.ttf') format('truetype');
    }
    @font-face {
        font-family: 'OpenSans';
        src: url('@/assets/fonts/OpenSans.ttf') format('truetype');
    }

        .inputs {
            position: relative;
            display: inline-block;
        }
    .dropdown {
        position: relative;
        width: 360px;
    }

    input {
        width: 100%;
        height: 60px;
        padding: 0 20px;
        box-sizing: border-box;
        font-size: 16px;
        color: rgba(255, 255, 255, 0.5);
        background: none;
        border: 1px solid white;
        border-radius: 10px;
        font-family: 'OpenSans';
    }

    .arrow_down {
        position: absolute;
        top: 23px;
        right: 23px;
        transition: transform 0.3s ease;
    }

    .arrow_down.rotated {
        transform: rotate(180deg);
    }

    .dropdown-menu {
        position: absolute;
        width: 100%;
        background: none;
        border: 1px solid white;
        border-radius: 10px;
        max-height: 200px;
        overflow-y: auto;
        list-style: none;
        margin: 0;
        padding: 0;
        z-index: 1000;
        color: rgba(255, 255, 255, 0.5);
        font-size: 20px;
        font-weight: normal;
        letter-spacing: 1px;
    }

    .dropdown-menu li {
        padding: 20px;
        cursor: pointer;
        border-bottom: 1px solid white;
    }

    .dropdown-menu li:hover {
        background-color: rgba(255, 255, 255, 0.052);
    }

    .modal {
        width: 1160px;
        height: 80vh;
        border-radius: 10px;
        position: relative; /* Обеспечиваем позиционирование для псевдоэлемента */
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 50px;
        z-index: 2;
        overflow-y: scroll;
        scrollbar-width: none;
        row-gap: 50px;
        @media (max-width: 900px) {
            width: 80vw;
            height: 60vh;
        }
        @media (max-width: 650px) {
            padding: 25px 20px;
            height: 300px;
        }
    }

    .modal::-webkit-scrollbar {
        width: 0;  
        height: 0;
    }

    .modal::-webkit-scrollbar-thumb {
        background: transparent;
    }


    .close {
        position: absolute;
        right: 30px;
        top: 30px;
        width: 21px;
        height: 21px;
        cursor: pointer;
        @media (max-width: 450px) {
            right: 20px;
            top: 20px;
        }
    }

    .modal::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url("@/assets/images/background.png");
        background-color: #1B1E3D;
        background-size: cover;
        background-position: center;
        background-blend-mode: multiply;
        opacity: 0.25; /* Прозрачность только для фона */
        background-size: 300%;
        filter: brightness(140%) contrast(80%);
        border-radius: 10px;
        @media (max-width: 900px) {
            width: 80vw;
            height: 60vh;
        }
        @media (max-width: 650px) {
            height: 300px;
        }
    }

    .text_wrapper {
        display: flex;
        flex-direction: column;
        align-items: start;
        row-gap: 30px;
    }

    h1 {
        font-size: 48px;
        color: white;
        font-family: 'Tektur';
        line-height: 62.4px;
        font-weight: 400;
        @media (max-width: 900px) {
            font-size: 35px;
        }
        @media (max-width: 500px) {
            font-size: 30px;
        }
    }
    span {
        font-size: 24px;
        font-family: 'OpenSans';
        font-weight: 400;
        line-height: 32.68px;
        color: white;
        text-wrap: wrap;
        text-align: left;
        font-family: 'OpenSans';
        @media (max-width: 900px) {
            font-size: 20px;
        }
        @media (max-width: 650px) {
            font-size: 17px;
        }
        @media (max-width: 550px) {
            font-size: 15px;
        }
        @media (max-width: 450px) {
            font-size: 13px;
        }
    }
    hr {
        width: 100%;
        height: 1px;
        background-color: white;
        @media (max-width: 900px) {
            width: 80%;
        }
    }

    .modal_cotainer {
        display: flex;
        width: 100%;
        justify-content: space-between;
    }
    .user_info {
        display: flex;
        column-gap: 30px;
        align-items: center;
    }
    .avatar {
        width: 130px;
        height: 130px;
        border-radius: 50%;
        object-position: center;
        object-fit: cover;
    }

    h2 {
        font-family: 'OpenSans';
        font-size: 20px;
        background: linear-gradient(to right, #7023EC, #A585DA); 
        -webkit-background-clip: text;
        background-clip: text;
        color: transparent;
    }

    span {
        color: white;
        font-size: 18px;
        font-family: 'OpenSans';
    }

</style>